const tools = require('../utils/tools'); // 
const applications = require('../utils/applications'); // 
const Super = require('../components-dom-super/g-super'); // ()

// 
const Sub = tools.constructorInherit(Super, {
    // 
    wrap: '.g-footer',
    // 
    callback: {},
    // 
    config: {
        showHeight: 200,
    },
    // 
    data: {},
});

// ()
Sub.prototype.moduleDomCreate = function () {
    this.moduleDom = applications.createElement({
        style: this.opts.config.moduleDomStyle,
        customAttribute: this.opts.config.moduleDomCustomAttribute,
        attribute: {
            className: 'g-go-top',
            innerHTML: '<div class="g-go-top-icon iconfont icon-up"></div>',
        },
    });
};

// ()
Sub.prototype.power = function () {
    const self = this;
    this.moduleDom.addEventListener('click', function () {
        applications.scrollToY('0');
    });
    window.addEventListener('scroll', function () {
        const scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        if (scrollTop >= self.opts.config.showHeight) {
            self.moduleDom.classList.add('g-go-top-active');
        } else {
            self.moduleDom.classList.remove('g-go-top-active');
        }
    });
};

module.exports = Sub;



