const tools = require('../utils/tools'); // 
const applications = require('../utils/applications'); // 
const Super = require('../components-dom-super/g-super'); // ()
const domAddPosition = require('zhf.dom-add-position');

// 
const Sub = tools.constructorInherit(Super, {
    // 
    callback: {
        click: function () {
        },
        moduleDomRenderBefore: function (self) {
            domAddPosition(self.wrapDom, 'relative');
        },
    },
    // 
    config: {
        isTransparent: false, // ()
        positionMethod: 'fixed', //  'fixed'() 'absolute'()
    },
    // 
    data: {},
});

// ()
Sub.prototype.moduleDomCreate = function () {
    const config = this.opts.config;
    let className = '';
    if (config.isTransparent) {
        className = 'g-mask-transparent';
    }
    if (config.positionMethod === 'fixed') {
        className = 'g-mask-fixed';
    }
    this.moduleDom = applications.createElement({
        style: this.opts.config.moduleDomStyle,
        customAttribute: this.opts.config.moduleDomCustomAttribute,
        attribute: {
            className: `g-mask ${className}`,
            innerHTML: '',
        },
    });
};

// ()
Sub.prototype.power = function () {
    const self = this;
    this.moduleDom.addEventListener('click', function (ev) {
        self.opts.callback.click();
        ev.stopPropagation();
    });
};

module.exports = Sub;



