const tools = require('../utils/tools'); // 
const applications = require('../utils/applications'); // 
const Super = require('../components-dom-super/g-super'); // ()

// 
const Sub = tools.constructorInherit(Super, {
    // 
    wrap: '.g-footer',
    // 
    callback: {},
    // 
    config: {},
    // 
    data: {},
});

// ()
Sub.prototype.moduleDomCreate = function () {
    this.moduleDom = applications.createElement({
        style: this.opts.config.moduleDomStyle,
        customAttribute: this.opts.config.moduleDomCustomAttribute,
        attribute: {
            className: 'g-copyright',
            innerHTML: `
                <div class="g-copyright-icon iconfont icon-logo"></div>
                <div class="g-copyright-text"></div>
            `,
        },
    });
};

// ()
Sub.prototype.power = function () {
    // ...
};

module.exports = Sub;



